a.btn:before,
.btn:before,
button:before {
	content: '';
	position: absolute;
	left: 50%;
	top: 50%;
	width: 0%;
	height: 0%;
	background: #2942a5;
	transition: all 0.6s cubic-bezier(0.3, 0, 0.3, 1);
	-webkit-transition: all 0.6s cubic-bezier(0.3, 0, 0.3, 1);
	transform: translate(-50%, -50%);
	-webkit-transform: translate(-50%, -50%);
	border-radius: 56px;
	-webkit-border-radius: 56px;
	opacity: 0;
	z-index: -1;
	filter: blur(10px);
}

a.btn:before,
.btn:before,
button:before {
	z-index: 1;
}

