	content: '';
	position: absolute;
	left: -10vw;
	top: -20vh;
	width: 120vw;
	height: 120vh;
	background: #fff;
}

.preloader .spinner {
	display: inline-block;
	width: 15px;
	height: 15px;
	background: #2942a5;
	border-radius: 15px;
	-webkit-animation: spinner 0.75s cubic-bezier(0.17, 0.37, 0.43, 0.67) infinite;
	animation: spinner 0.75s cubic-bezier(0.17, 0.37, 0.43, 0.67) infinite;
}

